$(document).ready(function () {
    

    $(".js-call-modal").click(function (e) {
        e.preventDefault()
        let needModalId = $(this).attr("data-modal-id")

        $("body").addClass("overflow-hidden")
        $(".modal").addClass("modal-is-visible")

        $('[data-id="'+needModalId+'"]').addClass("modal-is-visible")

        $(".modal__body").css("maxHeight", $(window).innerHeight() - 40+"px")

    })
    $(".modal__overlay, .modal__close").click(function (e) {
        $(".modal-is-visible").removeClass("modal-is-visible")
        $("body").removeClass("overflow-hidden")
    })


    $(".js-form-button").on("click", validate);

    function validateEmail(email) {
        let re = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
        return re.test(String(email).toLowerCase());
    }


    function validate() {
        let emailInput = $(".js-form-input-email");
        let email = $(".js-form-input-email").val();
        if (validateEmail(email)) {
            emailInput.addClass("js-input-success")
            localStorage.setItem('userEmail', email);
        } else {
            emailInput.addClass("js-input-error")
        }
        return false;
    }





})